import React, { useState } from 'react';
import { Form } from 'react-bootstrap';
import { Helmet } from 'react-helmet';
import ContentContainer from '../../../../components/ContentContainer/ContentContainer';
import { useCities } from '../../../Cities/hooks';
import InputsContainer from '../../../../components/InputsContainer/InputsContainer';
import { useNavigate, useParams } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { Dispatcher } from '../../../../types/store';
import { useForm } from 'react-hook-form';
import { OrderStatusForm } from '../../../../types/Edit';
import { useUploadedEntity } from '../../../../utils/helpers/hooks';

const PointEdit = (): React.ReactElement => {
  const navigate = useNavigate();
  const dispatch = useDispatch<Dispatcher>();
  const { orderStatusId } = useParams();
  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
  } = useForm<OrderStatusForm>({
    defaultValues: {
      name: '',
    }
  });
  const orderStatusUploading = useUploadedEntity(orderStatusId, 'order-status');
  const [cities, citiesLoading] = useCities();
  const [selectedCity, setSelectedCity] = useState<string>('Выберите город');

  return (
    <>
      <Helmet>
        <title>Need For Drive - Карточка пункта выдачи</title>
        <meta name="viewport" content="width=device-width, initial-scale=1" />
        <meta name="description" content="Home page" />
      </Helmet>
      <ContentContainer title="Карточка пункта выдачи">
        <InputsContainer title="Настройки пункта выдачи">
          <Form.Group controlId="pointName">
            <Form.Label>Название</Form.Label>
            <div className="input-wrap">
              <Form.Control
                type="text"
                placeholder="Введите название"
                aria-describedby="pointName"
              />
            </div>
          </Form.Group>
          <Form.Group controlId="pointAddress">
            <Form.Label>Адрес</Form.Label>
            <div className="input-wrap">
              <Form.Control
                type="text"
                placeholder="Введите адрес"
                aria-describedby="pointAddress"
              />
            </div>
          </Form.Group>
          <Form.Group controlId="pointCity">
            <Form.Label>Город</Form.Label>
            <div className="input-wrap">
              <Form.Select
                size="sm"
                value={selectedCity}
                onChange={(e) => setSelectedCity(e.target.value)}
                disabled={citiesLoading}
                aria-describedby="pointCity"
              >
                <option value="Выберите город">Выберите город</option>
                {cities && cities.map((city) => (
                  <option key={city.id} value={city.id}>{city.name}</option>
                ))}
              </Form.Select>
            </div>
          </Form.Group>
        </InputsContainer>
      </ContentContainer>
    </>
  );
};

export default PointEdit;
